@import "src/styles/colors"
.card, .firstCard
  width: 15%
  height: 145px
  border-radius: 17px
  padding: 20px
  display: flex
  flex-direction: column
  justify-content: space-between
  cursor: pointer
  @media only screen and (max-width: 1350px)
    width: 32%
  @media only screen and (max-width: 600px)
    width: 30%
    height: 120px
  @media only screen and (max-width: 600px)
    width: 30%
    height: 100px
  @media only screen and (max-width: 375px)
    width: 31%
    height: 90px
    padding: 15px

.firstCard
  background-color: $pink-FFE
  .title
    color: $pink-FF
  svg path
    fill: $pink-FF

.card
  background-color: $gray-F5

.card:hover
  background-color: $pink-FFE
  .title
    color: $pink-FF
  svg path
    fill: $pink-FF

.title
  width: 65%
  font-size: 15px
  line-height: 17px
  color: $blue-1C
  @media only screen and (max-width: 600px)
    font-size: 10px
    line-height: 12px


svg
  @media only screen and (max-width: 600px)
    width: 15px
    height: 17px
