@import "../../styles/colors"
.cardProduct
  width: 23%
  background-color: white
  padding: 25px
  display: flex
  flex-direction: column
  gap: 20px
  box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.05)
  border-radius: 25px
  @media only screen and (max-width: 1501px)
    width: 31%
  @media only screen and (max-width: 1150px)
    width: 47%
  @media only screen and (max-width: 768px)
    width: 100%


.topBlock
  display: flex
  justify-content: space-between

.imgCard
  width: 65px
  height: 65px

.countProduct
  display: flex
  align-items: center
  gap: 10px

.countProduct button
  display: flex
  align-items: center
  height: 20px

.countNumber
  height: 32px
  width: 26px
  background-color: $gray-F5
  border-radius: 9px
  display: flex
  align-items: center
  justify-content: center

.countNumber span
  font-size: 15px
  line-height: 18px
  color: $light-gray

.middleBlock
  display: flex
  flex-direction: column
  gap: 5px

.nameApp
  font-size: 18px
  font-weight: 700
  line-height: 20px

.descList
  display: flex
  flex-direction: column















